import $ from 'jquery';
var Deal = /** @class */ (function () {
    function Deal(vm) {
        this.vm = vm;
    }
    // eslint-disable-next-line
    Deal.prototype.setCard = function (sender, card) {
        sender.setHand(card);
    };
    Deal.prototype.dealCard = function (sender, card, isHidden) {
        var _this = this;
        var elements = sender.getElements();
        var dealerHand = this.vm.dealer.getHand();
        var playerHand = this.vm.player.getHand();
        if (dealerHand.find(function (e) { return e.index === card.index; }) || playerHand.find(function (e) { return e.index === card.index; }))
            return;
        this.vm.deal.setCard(sender, card);
        this.vm.renderCard(elements.ele, sender, false, isHidden, undefined);
        sender.getScore(function (score) { return $(elements.score).html(score); });
        if (playerHand.length < 3) {
            if (dealerHand.length > 0 && dealerHand[0].rank === 'A') {
                if ($('.insurance').length === 0 && !this.vm.isGameStarted()) {
                    this.vm.setState({ insurance: true });
                }
            }
            this.vm.player.getScore(function (score) {
                if (score === 21) {
                    if (_this.vm.blackjack)
                        return;
                    setTimeout(function () { return _this.vm.player.stand(); }, 500);
                    _this.vm.blackjack = true;
                }
            });
        }
    };
    return Deal;
}());
export default Deal;
