import $ from 'jquery';
import { toNumber } from './number';
export var resultPopup = function (game) {
    var status = game.status.toLowerCase();
    var popup = $("<div class='resultPopup resultPopup-".concat(status, "'>\n            <div class='multiplier'>").concat(Number(game.odds).toFixed(2), "x</div>\n            <div class='divider'></div>\n            <div class='profit'>").concat(game.profit === 0 ? '0.00' : toNumber(game.profit), "</div>\n        </div>\n    "));
    $('.game-content').append(popup);
    popup.hide().fadeIn('fast');
    setTimeout(function () {
        // eslint-disable-next-line
        popup.fadeOut('fast', function () {
            $(this).remove();
        });
    }, 2000);
};
export var Random = function (min, max, floor) {
    if (floor === void 0) { floor = true; }
    var r = Math.random() * max + min;
    return floor ? Math.floor(r) : r;
};
export var chain = function (times, ms, cb) {
    var i = 0;
    var next = function () {
        if (i < times) {
            setTimeout(function () {
                cb(i);
                next();
            }, ms);
            i += 1;
        }
    };
    next();
};
